"use client";

import { useCart } from "@/components/CartContext";
import { Button } from "@/components/ui/button";
import { host } from "@/utils/utils";
import { DeleteIcon, MinusIcon, PlusIcon } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { useParams } from "next/navigation";
import { orderService } from "@/lib/services/orderService";
import { useState } from "react";

export default function Page() {
  const {
    cartItems,
    incrementQuantity,
    decrementQuantity,
    removeFromCart,
    totalPrice,
  } = useCart();
  const params = useParams();
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const submitOrder = () => {
    const data = {
      items: cartItems,
      shop: params.restaurantId,
      total_price: totalPrice,
    };
    setIsLoading(true);
    orderService
      .addOrder(data)
      .then((res) => {
        setIsLoading(false);
      })
      .catch((err) => {
        setIsLoading(false);
        console.log(err);
      });
  };

  return (
    <div className="w-full h-full  h-screen">
      <div className="w-full flex items-center justify-center">
        <h1 className="mt-24 text-4xl font-bold text-[#C71F37]">Order</h1>
      </div>
      <div className="">
        {cartItems.map((data) => {
          return (
            <div
              className="relative w-full border-y-2 px-8 border-[#C71F37] py-4 flex items-center justify-between gap-4 "
              key={data.id}
            >
              <div className="w-32 h-32 rounded-lg">
                <Image
                  className="w-full h-full object-cover rounded-lg"
                  alt="alt"
                  src={`${host}/${data.image_url}`}
                  width={300}
                  height={300}
                />
              </div>
              <div className="flex items-center gap-3">
                <MinusIcon
                  className="text-[#C71F37] hover:text-[#000] cursor-pointer"
                  onClick={() => decrementQuantity(data.id)}
                />

                {data.quantity}
                <PlusIcon
                  className="text-[#C71F37] hover:text-[#000] cursor-pointer"
                  onClick={() => incrementQuantity(data.id)}
                />
              </div>
              <div>
                <h3>{data.price}</h3>
              </div>
              <DeleteIcon
                className="absolute top-2 right-2 cursor-pointer hover:text-[#000] text-[#C71F37] "
                onClick={() => removeFromCart(data.id)}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
}
